export function convertTwentyToMod(value){
    if(value >= 0 && value <= 20){
        const ref = [
            -5,-5, // 0, 1
            -4,-4, // 2, 3
            -3,-3, // 4, 5
            -2,-2, // 6, 7
            -1,-1, // 8, 9
            0,1, // 10, 11
            2,3, // 12, 13
            4,5, // 14, 15
            6,7, // 16, 17
            8,9, // 18, 19
            10 // 20
        ]

        return ref[value];
    }

    return undefined;
}
export function rollDice(dice) {
    const min = 1;
    dice = Math.floor(dice);
    return Math.floor(Math.random() * (dice - min +1)) + min;
}
export function rollDeltaDice(dice){
    const ref = [4, 6, 8, 10, 12, 20];
    
    if(ref.indexOf(dice) >= 0){
        const result = rollDice(dice);

        console.log("DeltaDice Result:", result);

        return result > 3;
    }

    return undefined;
}