//---IMPORT---//
import { 
    createHtml, createBtnSqr, createBtnSqrImg, createScrollBar,
    createField, createFieldContent,
    createNavBar, createActionBar, createMessage, createMsgContent, createSheetGrid, createCharacterSheet,
    HEXA_SVG_URL, USER_SVG_URL,
    createPage,
} from "./utils/components.js";

import { rollDice } from "./utils/dice.js";

if('serviceWorker' in navigator /*&& window.isSecureContext*/) {
    navigator.serviceWorker.register('./sw.js');
}

//---VARIABLE---//
const mainEl = document.querySelector("main");


//---MAIN---//
////
//
// Navigation Bar //
const navBar = createNavBar();
document.body.appendChild(navBar);

const navMainBtn = createBtnSqrImg(HEXA_SVG_URL, () => {
    navMainBtn.select();
    navCharBtn.unselect();

    switchPage(pageMain);
});
navMainBtn.select();

navBar.appendChild(navMainBtn.el);
const navCharBtn = createBtnSqrImg(USER_SVG_URL, () => {
    navCharBtn.select();
    navMainBtn.unselect();

    switchPage(pageChar);
});
navBar.appendChild(navCharBtn.el);

// Page Main //
const pageMain = createPage("main");

// action bar
const actionBar = createActionBar();
pageMain.appendChild(actionBar);

for(const dice of [20, 12, 10, 8, 6, 4]){
    const actionDiceBtn = createBtnSqr(`D${dice}`, () => {
        actionDiceBtn.select();
    
        // roll dice
        const result = rollDice(dice);
    
        displayMsg("Résultat", [createMsgContent(result, true), createMsgContent(`/ ${dice}`)])
    
        setTimeout(() => {
            actionDiceBtn.unselect();
        }, 1000);
    });
    actionBar.appendChild(actionDiceBtn.el);
}

// Page Character //
const pageChar = createPage("char");

const characterSheet = createCharacterSheet();
pageChar.appendChild(characterSheet);

const nameField = createField("Nom", createFieldContent("nom du personnage"));
characterSheet.appendChild(nameField);

const sheetGrid = createSheetGrid();
characterSheet.appendChild(sheetGrid);
const pvField = createField("PV", [
    createFieldContent("10", true),
    createFieldContent(12),
]);
sheetGrid.appendChild(pvField);
const lvlField = createField("NIV", createFieldContent(1));
sheetGrid.appendChild(lvlField);

const statSheetGrid = createSheetGrid();
characterSheet.appendChild(statSheetGrid);
const strField = createField("FOR", [
    createFieldContent(20),
    createFieldContent("+5", true),
]);
statSheetGrid.appendChild(strField);
const intField = createField("INT", [
    createFieldContent(20),
    createFieldContent("+5", true),
]);
statSheetGrid.appendChild(intField);
const dexField = createField("DEX", [
    createFieldContent(20),
    createFieldContent("+5", true),
]);
statSheetGrid.appendChild(dexField);
const perField = createField("PER", [
    createFieldContent(20),
    createFieldContent("+5", true),
]);
statSheetGrid.appendChild(perField);
const conField = createField("CON", [
    createFieldContent(20),
    createFieldContent("+5", true),
]);
statSheetGrid.appendChild(conField);
const chaField = createField("CHA", [
    createFieldContent(20),
    createFieldContent("+5", true),
]);
statSheetGrid.appendChild(chaField);

//
////


// MSG Layout //
const messageLayout = createHtml("div", {
    id: "message-layout",
});
document.body.appendChild(messageLayout);

switchPage(pageMain);

// Scroll Bar //
const scrollBar = createScrollBar();
document.body.appendChild(scrollBar.el);
scrollBar.init();


//---FUNCTION---//
function switchPage(pageEl){
    // remove old content
    while (mainEl.firstChild) {
        mainEl.removeChild(mainEl.firstChild);
    }

    mainEl.appendChild(pageEl);
}

function displayMsg(label, message){
    const messageEl = createMessage(label, message);

    messageLayout.appendChild(messageEl);

    const timer = setTimeout(() => {
        messageLayout.removeChild(messageEl);
    }, 4000);

    messageEl.addEventListener("click", (e) => {
        messageLayout.removeChild(messageEl);

        clearTimeout(timer);
    });
}