//---FUNCTION---//
/**
 * 
 * @param {string} tag div, p, img
 * @param {?object} attributes { class, child, events: { type, callback }, attributes }
 * @returns {HTMLElement}
 */
 export function createHtml(tag, attributes){
    const element = document.createElement(tag);

    if(attributes){
        for (const key in attributes) {
            if(key === "class"){
                if(Array.isArray(attributes[key]))
                    attributes[key].forEach(className => {
                        element.classList.add(className);
                    });
                else
                    element.classList.add(attributes[key]);
            }else if(key === "child"){
                if(Array.isArray(attributes[key]))
                    attributes[key].forEach(children => {
                        if(children instanceof Node)
                            element.appendChild(children);
                        else
                            element.append(children);
                    });
                else
                    if(attributes[key] instanceof Node)
                        element.appendChild(attributes[key]);
                    else
                        element.innerHTML = attributes[key];
            }else if(key === "events"){
                if(Array.isArray(attributes[key]))
                    attributes[key].forEach(event => {
                        element.addEventListener(event.type, event.callback);
                    });
                else
                    element.addEventListener(attributes[key].type, attributes[key].callback);
            }else{
                element.setAttribute(key, attributes[key]);
            }
        }
    }

    return element;
}

export function createBtnSqrImg(imgSrc, clickCb){
    const img = createHtml("img", {
        src: imgSrc,
    });

    const element = createBtnSqr(img, clickCb);
    
    return element;
}

export function createBtnSqr(content, clickCb){
    
    const element = {
        el: createHtml("div", {
            class: "btn-sqr",
            child: content,
            events: {
                type: "click",
                callback: function(){
                    if(typeof clickCb == "function"){
                        clickCb();
                    }
                }
            },
        }),
        select: function(){
            element.el.classList.add("selected");
        },
        unselect: function(){
            element.el.classList.remove("selected");
        },
        toggle: function(){
            element.el.classList.toggle("selected");
        }
    }

    return element;
}

export function createFieldContent(text, highlight){
    return createHtml("div", {
        class: highlight? ["field_content", "highlight"] : "field_content",
        child: text,
        contenteditable: true,
    });
}
export function createField(label, content){
    const labelEl = createHtml("div", {
        class: "field_label",
        child: label,
    });

    const element = createHtml("div", {
        class: "field",
        child: [
            labelEl,
        ]
    });

    if(Array.isArray(content))
        for(const item of content){
            element.appendChild(item);
        }
    else{
        element.appendChild(content);
    }

    return element;
}

