export function createScrollBar(){
    const barEl = createHtml("div", {
        class: "scroll-bar_item",
    });
    const containerEl = createHtml("div", {
        class: "scroll-bar",
        child: barEl,
    });

    let debounceResize;
    let totalPageHeight;
    let barHeight;
    let topMax;

    const element = {
        el: containerEl,
        init: function(){
            const parentEl = containerEl.parentNode;

            totalPageHeight = parentEl.scrollHeight - window.innerHeight;

            if(totalPageHeight == 0){
                element.el.style.display = "none";
                window.removeEventListener("scroll", onScroll);
            }else{
                element.el.style.display = "";
                window.addEventListener("scroll", onScroll, {
                    capture: true,
                    passive: true
                });
            }

            barHeight = containerEl.clientHeight * (window.innerHeight / parentEl.scrollHeight);
            
            barEl.style.height = barHeight + "px";

            topMax = containerEl.clientHeight - barHeight;
        }
    }

    window.addEventListener("resize", () => {
        clearTimeout(debounceResize);

        debounceResize = setTimeout(() => {
            element.init();
        }, 250);
    });

    return element;

    function onScroll(){
        let newProgressHeight = window.pageYOffset / totalPageHeight;

        barEl.style.top = `${topMax * newProgressHeight}px`;
    }
}

