export function createMsgContent(text, highlight){
    return createHtml("div", {
        class: highlight? ["msg-box_content", "highlight"] : "msg-box_content",
        child: text,
    });
}
export function createMessage(label, content){
    const labelEl = createHtml("div", {
        class: "msg-box_label",
        child: label,
    });

    const containerEl = createHtml("div", {
        class: "msg-box_container",
    });

    const element = createHtml("div", {
        class: "msg-box",
        child: [
            labelEl,
            containerEl,
        ],
    });

    if(Array.isArray(content))
        for(const item of content){
            containerEl.appendChild(item);
        }
    else{
        containerEl.appendChild(content);
    }

    return element;
}